.vuiIconButton {
  display: inline-block;
  border-radius: $sizeXxs;
  padding: $sizeXxs;
  line-height: 0; // Ensure icon sits in the center.
}

// Color
$color: (
  accent: var(--color-accent),
  primary: var(--color-primary),
  success: var(--color-success),
  warning: var(--color-warning),
  danger: var(--color-danger),
  neutral: var(--color-text),
  subdued: var(--color-subdued)
);

@each $colorName, $colorValue in $color {
  .vuiIconButton--#{$colorName} {
    color: $colorValue;
    background-color: rgba($colorValue, 1);

    &:hover {
      background-color: rgba($colorValue, 0.9);
    }

    &-isSelected {
      background-color: rgba($colorValue, 0.9);
    }
  }
}

.vuiIconButton-isDisabled {
  cursor: default;
  pointer-events: none;
  opacity: 0.5;
}

// Size
.vuiIconButton--xs {
  padding: $sizeXxs;
  height: 24px;
}

.vuiIconButton--s {
  padding: $sizeXs * 0.75;
  height: 28px;
}

.vuiIconButton--m {
  padding: $sizeXs;
  height: 34px;
}
