.vuiScreenBlock {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: $screenBlockZIndex;
  display: flex;
  align-items: center;
  justify-content: center;
}

.vuiScreenBlock__mask {
  width: 100%;
  height: 100%;
  background-color: transparentize($colorFullShade, 0.6);
}

.vuiScreenBlock--neutral {
  .vuiScreenBlock__mask {
    background-color: transparentize($colorFullShade, 0.6);
  }
}

.vuiScreenBlock--primary {
  .vuiScreenBlock__mask {
    background-color: rgba(var(--color-primary-rgb), 0.6);
  }
}

.vuiScreenBlock--success {
  .vuiScreenBlock__mask {
    background-color: rgba(var(--color-success-rgb), 0.6);
  }
}

.vuiScreenBlock--danger {
  .vuiScreenBlock__mask {
    background-color: rgba(var(--color-danger-rgb), 0.6);
  }
}
